<template>
  <div class="wrapper">
    <header-component :title="dataClass.className + ' / Tổng hợp'" icon="mdi-arrow-left"
                      @onClickNavIcon="$router.back()"></header-component>
    <div id="content-wrapper">
      <v-card id="time-picker" class="ma-5 pa-5">
        <v-row>
          <v-col cols="11" sm="5">
            <v-menu ref="menu" v-model="menu" :close-on-content-click="false" :return-value.sync="date"
                    transition="scale-transition" offset-y max-width="290px" min-width="290px">
              <template v-slot:activator="{ on, attrs }">
                <v-text-field v-model="date" label="Chọn thời gian" prepend-icon="mdi-calendar" readonly v-bind="attrs"
                              v-on="on"></v-text-field>
              </template>
              <v-date-picker v-model="date" type="month" no-title scrollable locale="vi" color="pink">
                <v-spacer></v-spacer>
                <v-btn text color="pink" @click="menu = false">Cancel</v-btn>
                <v-btn text color="pink" @click="$refs.menu.save(date)">OK
                </v-btn>
              </v-date-picker>
            </v-menu>
          </v-col>
        </v-row>
      </v-card>
      <v-card id="summary-table-wrapper" class="ma-5">
        <v-banner class="font-weight-bold pink--text">Bảng tổng hợp {{ date }}</v-banner>
        <div id="summary-table">
          <v-simple-table dense>
            <template v-slot:default>
              <thead>
              <tr bgcolor="#e91e63">
                <th class="text-center white--text">Tên học sinh</th>
                <th class="text-center white--text">Buổi học</th>
                <th class="text-center white--text">Tiền học ( vnđ )</th>
              </tr>
              </thead>
              <tbody>
              <tr v-for="student in students" :key="student.id">
                <td class="text-center">{{ student.name }}</td>
                <td class="text-center">{{ student.number }}</td>
                <td class="text-center">{{ student.total }}</td>
              </tr>
              </tbody>
            </template>
          </v-simple-table>
        </div>
      </v-card>
    </div>
    <snackbar-component :text.sync="errorMessage"></snackbar-component>
  </div>
</template>

<script>
import SnackbarComponent from "@/components/ulti/SnackbarComponent";
import HeaderComponent from "@/components/ulti/HeaderComponent";
import db from "@/script/firestore";
/* eslint-disable no-debugger */
export default {
  name: "SummaryComponent",
  components: {
    SnackbarComponent,
    HeaderComponent
  },
  data() {
    return {
      date: new Date().toISOString().substr(0, 7),
      menu: false,
      modal: false,
      errorMessage: '',
      dataClass: {},
      studentsData: {}
    }
  },
  created() {
    const classId = this.$route.params.classId;
    this.$bind('dataClass', db.collection('chickenClass')
        .doc(classId));
    this.$bind('studentsData', db.collection('chickenClass')
        .doc(classId).collection('data'));
  },
  computed: {
    students() {
      const key = this.date.replace('-', '_');
      return this.studentsData.map((v) => {
        const id = v.id;
        const name = v.studentName;
        const dataAtt = v.data || {};
        const dataMonth = dataAtt[key] || [];
        const dataMonthUnique = Array.from(new Set(dataMonth));
        const number = dataMonthUnique.length;
        const total = String(this.price * number).replace(/(.)(?=(\d{3})+$)/g, '$1,');
        return {id, name, number, total}
      });
    },
    price() {
      return this.dataClass.price || 0;
    }
  }
}
</script>

<style scoped>
#summary-table-wrapper {
  height: 67vh;
}

#summary-table {
  height: 58vh;
  overflow: scroll;
}
</style>