<template>
  <div class="wrapper">
    <header-component :title="dataStudent.studentName" icon="mdi-arrow-left"
                      @onClickNavIcon="$router.back()"></header-component>
    <div id="calendar-wrap">
      <FullCalendar class='demo-app-calendar' :options='calendarOptions'></FullCalendar>
    </div>
    <div class="pink--text text-center"> Click vào ngày để sửa nhé !</div>
    <snackbar-component :text.sync="errorMessage"></snackbar-component>
  </div>
</template>

<script>
import db from "@/script/firestore";
import FullCalendar from '@fullcalendar/vue'
import dayGridPlugin from '@fullcalendar/daygrid'
import interactionPlugin from '@fullcalendar/interaction'
import HeaderComponent from "@/components/ulti/HeaderComponent";
import SnackbarComponent from "@/components/ulti/SnackbarComponent";
/* eslint-disable no-debugger */
export default {
  components: {
    FullCalendar,
    HeaderComponent,
    SnackbarComponent
  },
  name: "StudentDataMonthComponent",
  created() {
    const classId = this.$route.params.classId;
    const studentId = this.$route.params.studentId;
    this.$bind('dataStudent', db.collection('chickenClass')
        .doc(classId).collection('data').doc(studentId));
  },
  data() {
    return {
      dataStudent: {},
      selectedDate: null,
      errorMessage: ''
    }
  },
  computed: {
    studentsRef() {
      const classId = this.$route.params.classId;
      const studentId = this.$route.params.studentId;
      return db.collection('chickenClass').doc(classId).collection('data').doc(studentId);
    },
    dates() {
      const dataStudent = this.dataStudent['data'] || {};

      let result = [];
      Object.keys(dataStudent).forEach((k) => {
        const keyMothYear = k.split('_');
        const year = keyMothYear[0];
        const month = keyMothYear[1];
        const dataMonth = dataStudent[k] || []
        result = result.concat(dataMonth.map((date) => {
          date = date < 10 ? "0" + date : date;
          return `${year}-${month}-${date}`;
        }));
      });
      return result;
    },
    calendarOptions() {
      const events = this.dates.map((date) => {
        return {
          title: 'OK',
          date: date
        }
      });
      return {
        plugins: [
          dayGridPlugin,
          interactionPlugin
        ],
        height: '70vh',
        initialView: 'dayGridMonth',
        events: events,
        eventColor: '#e91e63',
        locale: 'vn',
        firstDay: 1,
        headerToolbar: {
          start: 'title',
          center: '',
          end: 'today prev,next'
        },
        themeSystem: 'bootstrap',
        titleFormat: {month: 'numeric', year: 'numeric'},
        dateClick: this.handleDateClick,
        eventClick: this.handleEventClick,
      }
    }
  },
  methods: {
    async handleDateClick(args) {
      this.errorMessage = '';
      let selectedDate = args.date.getDate();
      let selectedMonth = args.date.getMonth() + 1;
      selectedMonth = selectedMonth < 10 ? '0' + selectedMonth : selectedMonth;
      let selectedMonthYear = args.date.getFullYear();
      let key = selectedMonthYear + '_' + selectedMonth;
      if (this.isAttDate(args.dateStr)) {
        await this.removeAtt(selectedDate, key);
      } else {
        await this.addAtt(selectedDate, key);
      }
    },
    isAttDate(dateStr) {
      return this.dates.includes(dateStr);
    },
    async handleEventClick(args) {
      this.errorMessage = '';
      const date = args.event.start.getDate();
      let selectedMonth = args.event.start.getMonth() + 1;
      selectedMonth = selectedMonth < 10 ? '0' + selectedMonth : selectedMonth;
      let selectedMonthYear = args.event.start.getFullYear();
      let key = selectedMonthYear + '_' + selectedMonth;
      try {
        await this.removeAtt(date, key);
      } catch (err) {
        this.errorMessage = err;
      }
    },
    async removeAtt(date, key) {
      const data = this.dataStudent['data'];
      data[key] = data[key].filter((d) => {
        return d !== date
      });
      try {
        await this.studentsRef.update({
          data: data
        });
      } catch (err) {
        this.errorMessage = err;
      }
    },
    async addAtt(date, key) {
      const data = this.dataStudent['data'] || {};
      data[key] = data[key] || [];
      data[key].push(date);
      try {
        await this.studentsRef.update({
          data: data
        });
      } catch (err) {
        this.errorMessage = err;
      }
    }
  }
}
</script>

<style>
#calendar-wrap {
  margin-top: 30px;
}

.fc-toolbar-title {
  color: #e91e63 !important;
}
</style>
